}





.content {
  position: relative;
  margin-bottom: 25px;
}

.content h2 {
  position: absolute;
  color: #fff;
  transform: translate(-50%, -50%);
  font-size: 1.3rem;
 
}

.content h2:nth-child(1) {
  color: transparent;
  -webkit-text-stroke: 1px #155c33;
}

.content h2:nth-child(2) {
  color:#477e20;
  animation: animate 4s ease-in-out infinite;
  
}

@keyframes animate {
	0%,
	100% {
		clip-path: polygon(
			0% 45%,
			16% 44%,
			33% 50%,
			54% 60%,
			70% 61%,
			84% 59%,
			100% 52%,
			100% 100%,
			0% 100%
		);
	}

	50% {
		clip-path: polygon(
			0% 60%,
			15% 65%,
			34% 66%,
			51% 62%,
			67% 50%,
			84% 45%,
			100% 46%,
			100% 100%,
			0% 100%
		);
	}
}





