.card-read {
  cursor: pointer;
  display: none;
  font-size: 0.13rem;
  font-weight: 700;
  letter-spacing: 1px;
  margin: 0 0 0.2rem;
  position: relative;
  text-align: right;
  text-transform: uppercase;
}

.card-read::before {
  background-color: #b8bddd;
  content: "";
  display: block;
  height: 2px;
  position: absolute;
  top: -2px;
  left: 0px;
  width: 100%;
  /* width: 70%; */
}

.card-tag {
  float: right;
