.btn-group {
  border-radius: 1rem;
  text-transform: capitalize;
}
.btn-group__item {
  border: none;
  /* min-width: 6rem; */
  padding: 0.2rem 0.4rem;
  background-color: rgb(121, 235, 93);
  cursor: pointer;
  margin: 0;
  font-size: 17px;
  box-shadow: inset 0px 0px 0px -15px rgb(51, 153, 76);
  transition: all 300ms ease-out;
  text-transform: capitalize;
}
.btn-group__item:first-child {
  border-top-left-radius: 0.15rem;
  border-bottom-left-radius: 0.15rem;
}
.btn-group__item:last-child {
  border-top-right-radius: 0.15rem;
  border-bottom-right-radius: 0.15rem;
}
.btn-group__item:hover,
.btn-group__item:focus {
  color: rgb(245, 8, 8);
  box-shadow: inset 0px -20px 0px -15px rgb(245, 131, 0);
}
.btn-group__item:focus {
  outline: none;
}
