@media (max-width: 998px) {
  .card-description {
    height: 50px;
  }
}

@media (max-width: 825px) {
  .main-card--cointainer {
    display: grid;
    grid-template-columns: repeat(2, 40%);
    grid-gap: 50px;
  }
  .batches {
    font-size: 0.2rem;
    margin-bottom: -3rem;
    padding-bottom: 2rem;
  }
  .batches-navbar {
    margin-top: 1rem;
  }
  .card-description {
    font-size: 0.22rem;
  }
  .card-title {
    font-size: 0.6rem;
  }
  .card-container .icon {
    font-size: 0.8rem;
  }
}
@media (max-width: 800px) {
  .batches {
    margin-bottom: -4rem;
    padding-bottom: 3rem;
  }
}

