/* Bubbles */
.bubble {
  position: absolute;
  bottom: -15px;
  border-radius: 50%;
  pointer-events: none;
  box-shadow: inset 0px 0px 70px rgba(255, 255, 225, 0.384);
  animation: pop 4s linear infinite;
  background-color: rgba(255, 255, 255, 0.2);
  border: solid 2px rgba(255, 255, 255, 0.4);
  transform-origin: 30px;
}

@keyframes pop {
  0% {
    transform: translateY(0px) rotate(0deg);
    opacity: 1;
  }
  80% {
    opacity: 0.3;
  }
  100% {
    transform: translateY(-103vh) rotate(360deg);
    opacity: 0;
  }
}

