/* Icons */
.icons {
  color: black;
}
.icons ul {
  display: flex;
  list-style: none;
  grid-gap: 0.4rem;
  gap: 0.4rem;
  font-size: 0.35rem;
  transform: rotate(0deg) skew(0deg);
  position: relative;
  /* left: 0.8rem; */
  top: 0.3rem;
}
.icons ul li span .fab {
  text-shadow: rgb(170, 166, 157) 0px 0px 0px, rgb(170, 166, 157) -1px 1px 0px,
    rgb(170, 166, 157) -2px 2px 0px, rgb(170, 166, 157) -3px 3px 0px,
    rgb(170, 166, 157) -4px 4px 0px;
}
.icons ul li {
  width: 0.7rem;
  height: 0.7rem;
  position: relative;
  /* background-color: #ccc; */
}
.icons ul li:nth-child(1) {
  color: rgb(39, 137, 250);
}
.icons ul li:nth-child(2) {
  color: rgb(30, 140, 243);
}
.icons ul li:nth-child(3) {
  color: rgb(248, 103, 188);
}
.icons ul li span {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  transition: 1s;
  border-radius: 7px;
  padding-left: 7px;
  padding-bottom: 7px;
}
.icons ul li span:nth-child(5) {
  display: none;
}
.icons ul li span:nth-child(4) {
  display: none;
}
.icons ul li:hover span:nth-child(5) {
  transform: translate(25px, -25px);
  opacity: 0.8;
}
.icons ul li:hover span:nth-child(4) {
  transform: translate(20px, -20px);
  opacity: 0.6;
}
.icons ul li:hover span:nth-child(3) {
  transform: translate(7px, -7px);
  opacity: 0.7;
}
.icons ul li:hover span:nth-child(2) {
  transform: translate(3px, -3px);
  opacity: 0.4;
}
.icons ul li:hover span:nth-child(1) {
  transform: translate(12px, -12px);
  opacity: 1;
  z-index: 100;
}
.icons ul li span:nth-child(1) {
  z-index: 1;
}
.icons ul li:hover:nth-child(1) span {
  background-color: #0461c4;
}
.icons ul li:hover:nth-child(2) span {
  background-color: #1d9bf0;
}
.icons ul li:hover:nth-child(3) span {
  background: linear-gradient(130deg, #365adb 20%, #fd2b93, #ff5819, #ffffff);
  /* background-color: #fc7540; */
}
.icons ul li:hover:nth-child(4) span {
  background-color: black;
}

